import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  message
  // Checkbox
} from 'antd'
import moment from 'moment'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSelect, CsxMultipleSearch} from 'Form'
import {API_PRODUCTS_KEYVALUES, API_PRODUCT_CATEGORYSONS, API_SHIPPEDORDERS_STATISTICKEYVALUES, API_USER_WAREHOUSELIST, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
import {PICKINGTYPE} from 'Mock'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.sendNumGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    console.log(values, 111)
    update({searchParams: {...searchParams, ...values}})
  }
  componentDidMount() {
    const {userLoation, update} = this.props
    console.log(userLoation)
    update({warehouseData: {value: userLoation.value, children: userLoation.children}})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams, userLoation} = this.props
    let reqObj = {...searchParams}
    // if (!(reqObj.hasOwnProperty('bigCategoryCode') || reqObj.hasOwnProperty('productCode'))) {
    //   message.error('请选择大类或者商品编码')
    //   return
    // }
    if (!((reqObj.bigCategoryCode && reqObj.bigCategoryCode.value) || (reqObj.productCode && reqObj.productCode.value))) {
      message.error('请选择大类或者商品编码')
      return
    }

    //设置业务类型和商品输入时间为必选项
    reqObj.businessTypeCode && reqObj.initDate ? handleGetList({searchParams: {...reqObj}, userLoation}) : message.error('业务类型和商品更新时间为必选项')
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }

  render() {
    const {
      searchParams: {
        warehouseCode,
        businessTypeCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        initDate,
        pickingType,
        productCode
      },
      searchParams,
      warehouseData,
      userLoation
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='查询地点'
              require
              selectItem={{data: warehouseCode ? warehouseCode : warehouseData, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>

          <Col span={16}>
            <Row type="flex">
              <CsxSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                antd={{disabled: searchParams.productCode && searchParams.productCode.value}}
                require = {searchParams.productCode && searchParams.productCode.value ? false : true}
                placeholder='大类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxSelect
                placeholder='中类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                antd={{disabled: searchParams.productCode && searchParams.productCode.value}}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                errMessage='请先选择大类'
                style={{marginLeft: 10, width: 163}}
              />
              <CsxSelect
                placeholder='小类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                antd={{disabled: searchParams.productCode && searchParams.productCode.value}}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                errMessage='请先选择中类'
                style={{marginLeft: 10, width: 163}}
              />
            </Row>
          </Col>
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_SHIPPEDORDERS_STATISTICKEYVALUES}
              require
              label='业务类型'
              searchParams={{orderType: 'T'}}
              selectItem={{data: businessTypeCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessTypeCode: value})
              }}
            />
          </Col>
          <Col span={16}>
            <div className='commonInput'>
              <label className="require">商品更新时间:</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker
                  style={{width: '500px'}}
                  ranges={{当前时间: [moment('00:00:00', 'HH:mm:ss'), moment()]}}
                  showTime={{
                    defaultValue: [moment('00:00:00', 'HH:mm:ss'), moment('23:59:59', 'HH:mm:ss')]
                  }}
                  onChange={(mo, string) => {
                    this.onInputChange({initDate: mo})
                  }}
                  format={'YYYY-MM-DD HH:mm:ss'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              data={PICKINGTYPE}
              label='分拣方式'
              selectItem={{data: pickingType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({pickingType: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              disabled={searchParams.bigCategoryCode && searchParams.bigCategoryCode.value}
              searchParamsKey='productCode'
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={16}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



