import React, { PureComponent } from 'react'
import {
  Table,
  Modal,
  message,
  Icon,
  Popover
} from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({ ...state.sendNumGather, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode',
    }, {
      title: '商品名称',
      width: 200,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '商品大类',
      width: 150,
      dataIndex: 'bigCategoryName',
      key: 'bigCategoryName',
    }, {
      title: '中类',
      width: 150,
      dataIndex: 'centreCategoryName',
      key: 'centreCategoryName'
    }, {
      title: '小类',
      width: 150,
      dataIndex: 'smallCategoryName',
      key: 'smallCategoryName'
    }, {
      title: '总量',
      width: 150,
      dataIndex: 'totalQty',
      key: 'totalQty'
    }, {
      title: '分拣方式',
      width: 150,
      dataIndex: 'pickType',
      key: 'pickType'
    }]
  }

  handleOperator(text, record, index, type) {
    const { handleGetList, searchParams, page, size, handleOperator } = this.props
    confirm({
      title: '温馨提示',
      content: `是否${type ? '启用' : '禁用'}该条信息？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          data: {
            id: record.id,
            warehouseStatus: type
          },
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({ searchParams: { ...searchParams } })
        })
      }
    })
  }

  render() {
    const {
      total,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows})
      },
    };
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          rowSelection={rowSelection}
          dataSource={searchResult}
          scroll={{ x: 'max-content', y: 400 }}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



