import React, { PureComponent } from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  Input,
  message
} from 'antd'
import { CsxSelect, CsxValidateInput } from 'Form'
import { API_USER_WAREHOUSELIST, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE, API_STOCKRELOCATION_SHIPPERKEYVALUES, API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES } from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm

@connect(
  state => ({ ...state.sendNumGather, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '商品编码',
      width: 100,
      key: 'productCode',
      dataIndex: 'productCode',
    }, {
      title: '商品名称',
      width: 250,
      dataIndex: 'productName',
      key: 'productName',
    }, {
      title: '单位',
      width: 150,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '调拨数',
      width: 150,
      key: 'transferQty',
      render: (text, record, index) => {
        return (
          <span>
            <CsxValidateInput style={{ width: '100px' }}
              value={record.transferQty ? record.transferQty.value == undefined ? record.transferQty : record.transferQty.value : ''}
              reg={'REG'}
              error={record.transferQty && record.transferQty.error}
              handleOnchange={(data) => {
                record.transferQty = data
                this.forceUpdate()
              }}
            />
          </span>
        )
      }

    }, {
      title: '可用数',
      width: 150,
      dataIndex: 'availableQty',
      key: 'availableQty',
    }, {
      title: '缺货数',
      width: 150,
      dataIndex: 'shortageQty',
      key: 'shortageQty',
      render: (text, record, index) => <span style={text > 0 ? { color: 'red' } : null}>{text}</span>
    }]
  }
  componentDidMount() {
    const { isShowCreateModal } = this.props
    console.log(isShowCreateModal)
  }

  onChangeReturnNum = (e) => {
    const { update } = this.props
    update({ availableQty: e.target.value })
  }

  onInputChange(values) {
    const { popData, update } = this.props
    update({ popData: { ...popData, ...values } })
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowCreateModal: false })
  }

  // 创建调拨
  handleCommit = () => {
    const { searchParams, userLoation, ModalsearchResult, handleCreate, warehouseData } = this.props
    let req = { items: [] }
    let res = JSON.parse(JSON.stringify(ModalsearchResult))
    res.map((o) => {
      req.items.push({
        productCode: o.productCode,
        qty: o.transferQty && o.transferQty.value || o.transferQty
      })
    })
    req.header = {
      sendLocationCode: userLoation.value,
      sendLocationName: userLoation.children,
      receiveLocationCode: searchParams.warehouseCode ? searchParams.warehouseCode.value : warehouseData.value,
      receiveLocationName: searchParams.warehouseCode ? searchParams.warehouseCode.children : warehouseData.children,
    }
    handleCreate({ data: req }, (data) => {
      const { handleGetList, searchParams, userLoation, update } = this.props
      if (data.success) {
        Modal.success({
          content: `调拨单创建成功,单号为${data.transferCode}`,
          okText: '确定',
          onOk() {
            update({ isShowCreateModal: false })
            handleGetList({ searchParams: { ...searchParams, userLoation } })
          }
        })
      } else {
        let errorText = data.errorMessage.split(',').map(t => <p>{t}</p>)
        console.log(errorText)
        Modal.error({
          content: errorText,
          okText: '确定',
        })
        return
      }
    })
  }

  // 刷新，重新获取一键调拨汇总
  handleUpData = () => {
    const {handleAllList, modalDate} = this.props
    console.log(modalDate)
    handleAllList({data: modalDate})
  }

  render() {
    const {
      userLoation,
      searchParams,
      popData: {
        receiveLocationCode
      },
      tableModalLoading,
      ModalsearchResult,
      modalTuttonLoading,
      update,
      warehouseData
    } = this.props
    update({ ModalsearchResult: ModalsearchResult })
    return (
      <Modal
        title="调拨明细"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <Row type="flex" justify="start" style={{ marginBottom: '15px' }}>
          <Col>
            <Button type="primary" onClick={this.handleCommit} loading={modalTuttonLoading}  >创建调拨</Button>
            <Button type="primary" onClick={this.handleUpData} style={{ marginLeft: '10px' }}  >刷新</Button>
          </Col>
        </Row>
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={12}>
              <span>出库地点：</span><span style={{ fontWeight: 'bold' }}>{userLoation.value}{userLoation.children}</span>
            </Col>
            <Col span={12}>
              <span>收货地点：</span><span style={{ fontWeight: 'bold' }}>{`${searchParams.warehouseCode ? searchParams.warehouseCode.value : warehouseData.value}${searchParams.warehouseCode ? searchParams.warehouseCode.children : warehouseData.children}`}</span>
              {/* <CsxSelect
                dateApi={API_USER_WAREHOUSELIST}
                label='收货地点'
                require
                selectItem={{ data: receiveLocationCode?receiveLocationCode:
                  {value:searchParams.warehouseCode.value, children:searchParams.warehouseCode.children}, 
                  key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName'] 
                }}
                onChangeSelect={(value) => {
                  if (value.value === userLoation.value) {
                    message.error('出库地点和收货地点不能相同')
                  }
                  this.onInputChange({ receiveLocationCode: value })
                }}
              /> */}
            </Col>
          </Row>
          <div className='table-group' style={{ margin: '10px 0' }}>
            <Table
              rowKey={(record, index) => record.id}
              loading={tableModalLoading}
              columns={this.columns}
              scroll={{ x: 720, y: 400 }}
              dataSource={ModalsearchResult}
              pagination={false}
            />
          </div>
        </div>
      </Modal>
    )
  }
}



