import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPEDORDERS_STATISTIC_PICKQTY_PAGE, API_SHIPPEDORDERS_STATISTIC_PICKQTY_EXPORT, API_ONEKEYTRANSFER_CREATE, API_ONEKEYTRANSFER_LOADLIST, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.SENDNUMGATHER_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.SENDNUMGATHER_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  delete reqObj.initDate
  getData({api: API_SHIPPEDORDERS_STATISTIC_PICKQTY_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    // let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.SENDNUMGATHER_UPDATE,
      data: {
        tableLoading: false,
        total: data.result.length,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.SENDNUMGATHER_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  delete reqObj.initDate
  getData({api: API_SHIPPEDORDERS_STATISTIC_PICKQTY_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.SENDNUMGATHER_FILE
    })
  })
}

// 创建一键调拨订单
export const handleCreate = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.SENDNUMGATHER_UPDATE, data: {tableModalLoading: true}})
  getData({api: API_ONEKEYTRANSFER_CREATE, ...reqData}).then(data => {
    dispatch({
      type: actionType.SENDNUMGATHER_UPDATE,
      data: {
        tableModalLoading: false,
        errorMessage: data.result
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.SENDNUMGATHER_FILE
    })
  })
}

// 汇总一键调拨订单
export const handleAllList = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.SENDNUMGATHER_UPDATE, data: {tableModalLoading: true}})
  getData({api: API_ONEKEYTRANSFER_LOADLIST, ...reqData}).then(data => {
    dispatch({
      type: actionType.SENDNUMGATHER_UPDATE,
      data: {
        tableModalLoading: false,
        ModalsearchResult: data.result || []
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.SENDNUMGATHER_FILE
    })
  })
}
// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.SENDNUMGATHER_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.SENDNUMGATHER_FILE
    })
  })
}



