import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.sendNumGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  handleExport = () => {
    const {handleExport, searchParams, userLoation} = this.props
    handleExport({searchParams: {...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}, userLoation}, () => {
      message.success('导出成功')
    })
  }

  //一键调拨功能
  handleTransfer = () => {
    const {update, selectedRows, searchParams, handleAllList, userLoation, warehouseData} = this.props
    if (searchParams.warehouseCode ? searchParams.warehouseCode && searchParams.warehouseCode.value == userLoation.value : warehouseData.value == userLoation.value) {
      message.error('出库地点和收货地点不能相同')
      return
    }
    if (!selectedRows.length) {
      message.error('请先选择需要一键调拨的商品')
      return
    }
    selectedRows.map(o => {
      o.locationCode = searchParams.warehouseCode && searchParams.warehouseCode.value
      o.qty = o.totalQty
    })
    handleAllList({data: selectedRows})
    update({isShowCreateModal: true, modalDate: selectedRows})
  }
  render() {
    const {total, userLoation, allotBottonPower, outWarehouseListSendNumGatherExport} = this.props
    return (
      <div className={style['table-tool']}>
        <Row>
          <Col span={12}>
            {
              outWarehouseListSendNumGatherExport && <Button type="primary" className={style.btn} onClick={() => {
                this.handleExport()
              }}>导出</Button>
            }
            {
              userLoation.type == 2 && allotBottonPower ?
                <Button type="primary" className={style.btn} onClick={() => {
                  this.handleTransfer()
                }}>一键调拨</Button> : null
            }
          </Col>
          <Col span={12}>
            <h3 style={{float: 'right'}}>查询结果：{total}</h3>
          </Col>
        </Row>
      </div>
    )
  }
}



